export default{
	methods:{
		to(where){
			uni.navigateTo({
				url:`/pages/mine/${where}`
			})
		},
		quit(){
			uni.removeStorageSync('userInfo')
			this.$msg('退出成功')
			setTimeout(function(){
				uni.navigateBack({
					delta:1
				})
			},1000)
		}
	}
}
